
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { IProductStatusForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import ProductStatusForm from '@/views/system/localization/product-status/forms/ProductStatusForm.vue'

export interface IData {
  model: IProductStatusForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: IProductStatusForm) => {
      return store.dispatch('productStatus/createForm', dataForm)
    }) as ResponseFetching<Promise<IProductStatusForm>>

    const form = ref()

    return {
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    ProductStatusForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        sortOrder: 0,
        description: '',
        name: 'New Product status'
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'product-status' })
    },

    async handleSubmit () {
      if (this.form && await this.form.validate()) {
        const data: IProductStatusForm = await this.saveForm(this.model)
        if (typeof data !== 'undefined') {
          this.model = data
        }

        if (this.model?.id) {
          this.$router.push({ name: 'product-status-update', params: { id: this.model.id } })
        }
      }
    }
  }
})
