
import { defineComponent, PropType, reactive, ref } from 'vue'
import { IProductStatusForm } from '@/types'

export interface IData {
  model: IProductStatusForm;
  isValid: boolean;
}

export default defineComponent({
  name: 'ProductStatusForm',

  setup () {
    return {
      form: ref(),
      rules: reactive({
      })
    }
  },

  props: {
    modelValue: Object as PropType<IProductStatusForm>,

    isLoading: Boolean as PropType<boolean>,

    isNew: {
      type: Boolean,
      default: false
    }
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || {
        sortOrder: 0,
        name: '',
        description: ''
      },

      isValid: false
    }
  },

  methods: {
    async validate (): Promise<boolean> {
      if (this.form) {
        // Инициация валидации для lazy-validation
        await this.form.validate()
      }

      // После вызова валидации изменит своё значение
      return this.isValid
    }
  }
})
